:root{
    --bg1:#ff2222;
    --bg2:#00ccff;
    --bg3:#22e622;
    --i:2;
  }
  
.CreateTodoButton {
    background-color: var(--bg2);
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
    z-index: 2;
  
    transform: rotate(0);
    transition: .3s ease;
}
  
.CreateTodoButton.rotated {
    transform: rotate(224deg);
}